header .container {
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	height: 100%;
}

.header-logo {
	flex: 2 1;
}
.header-menu {
	flex: 8 1;
}
.header-login {
	flex: 2 1;
}

.header-menu span,
.header-login span {
	margin: 0 20px;
	display: inline-block;
	line-height: 1;
	text-decoration: none;
	cursor: pointer;
	color: white;
	-webkit-transition: color 0.3s ease-in-out;
	-moz--transition: color 0.3s ease-in-out;
	transition: color 0.3s ease-in-out;
}

.header-menu span:hover,
.header-login span:hover {
	color: gray;
	-webkit-transition: width, color 0.3s ease-in-out;
	-moz--transition: width, color 0.3s ease-in-out;
	transition: width, color 0.3s ease-in-out;
}

.header-menu span:after,
.header-login span:after {
	position: relative;
	background-color: red;
	display: block;
	content: '';
	top: 5px;
	height: 2px;
	width: 0%;
	-webkit-transition: width 0.3s ease-in-out;
	-moz--transition: width 0.3s ease-in-out;
	transition: width 0.3s ease-in-out;
}

.header-menu span:hover:after,
.header-menu span:focus:after,
.header-login span:hover:after,
.header-login span:focus:after {
	width: 100%;
}

